<template>
  <div v-loading="loading">
    <template v-if="!loading">

      <div class="chart-box">
        <chart-bar
          :xHeading="chartData.headings"
          :xList="chartData.values"
          :xAxisName="$t('order-meeting.statistics.product_name')" />
      </div>
      <p class="chart-title">{{ $t('order-meeting.statistics.top10_product_data') }}</p>
      <div class="export flex-center"
        @click="getExportData"
        v-loading="loadingExport">
        {{$t('order-meeting.statistics.export_data')}}
      </div>
      <div class="table"
        v-for="collectionList in collectionOrderList"
        :key="collectionList.collectionName">
        <el-table
          :data="collectionList.shoeList">
          <el-table-column
            :prop="shoeName"
            :label="collectionList.collectionName">
            <template slot-scope="scope">
              {{ scope.row.shoeName+'/'+ scope.row.colorName }}
            </template>
          </el-table-column>
          <el-table-column
            prop="shoeNumber"
            width="175"
            :label="`${
              $t('order-meeting.statistics.total')
              }：${
                collectionList.totalNumber
              }${
                $t('order-meeting.statistics.units')
              }`">
            <template slot-scope="scope">
              {{ scope.row.shoeNumber }}{{$t('order-meeting.statistics.units')}}
            </template>
          </el-table-column>
        </el-table>
      </div>
    </template>
  </div>
</template>

<script>
import Vue from 'vue';
import sortBy from '_/sort-by';
import exec from '@/utils/exec';
import ChartBar from '@/components/echart/ChartBar';

import { getCollectionOrderList, getExportData } from '@/api/order-meeting';

export default Vue.extend({
  components: {
    ChartBar,
  },
  props: {
    workspaceId: {
      default() {
        return this.$route.query.workspaceId;
      },
    },
  },
  data() {
    return {
      collectionOrderList: [],
      chartData: {
        headings: [],
        values: [],
      },
      loading: false,
      loadingExport: false,
    };
  },
  created() {
    this.getCollectionOrderList();
  },
  methods: {
    async getCollectionOrderList() {
      this.loading = true;

      this.collectionOrderList = (await getCollectionOrderList(this.workspaceId)).data;

      if (this.collectionOrderList.length === 0) {
        this.$emit('noData');
      }

      let list = [];
      this.collectionOrderList.forEach(series => {
        series.shoeList.forEach(item => {
          list.push(item);
        });
      });
      list = sortBy(list, 'shoeNumber').reverse().slice(0, 10);
      list.forEach(item => {
        this.chartData.headings.push(`${item.shoeName}/${item.colorName}`);
        this.chartData.values.push(item.shoeNumber);
      });

      this.loading = false;
    },

    // 获取导出文件地址
    async getExportData() {
      if (this.loadingExport) return;
      this.loadingExport = true;
      const { data: url } = await getExportData(this.workspaceId);
      if (/QtWebEngine/i.test(navigator.userAgent)) {
        exec('downloadStaticFile', url);
      }
      // 创建a标签
      await this._createDownloadTag(url);
      this.loadingExport = false;
    },

    _createDownloadTag(url) {
      let domA = document.createElement('a');
      domA.href = url;
      domA.download = 'file';
      domA.target = '__blank';
      domA.click();
      domA = null;
    },
  },
});
</script>

