<template>
  <div v-loading="loading">
    <template v-if="!loading">
      <div class="chart-box">
        <chart-bar
          :xHeading="chartData.headings"
          :xList="chartData.values"
          :xAxisName="$t('order-meeting.statistics.collection')"
          barWidth="20%"
          :xLableRotate="0" />
      </div>
      <!-- <p class="chart-title">TOP10鞋款数据</p> -->
      <div class="table">
        <el-table :data="collectionOrderList">
          <el-table-column
            :label="$t('order-meeting.statistics.collection_sales_ranking')">
            <template slot-scope="scope">
              {{`TOP${scope.$index + 1}`}}
            </template>
          </el-table-column>
          <el-table-column
            prop="collectionName"
            :label="$t('order-meeting.statistics.collection')">
          </el-table-column>
          <el-table-column
            prop="totalNumber"
            width="175"
            :label="`${
              $t('order-meeting.statistics.total')
              }：${
                totalShoes
              }${
                  $t('order-meeting.statistics.units')
              }`">
            <template slot-scope="scope">
              {{ scope.row.totalNumber }}{{$t('order-meeting.statistics.units')}}
            </template>
          </el-table-column>
        </el-table>
      </div>
    </template>
  </div>
</template>

<script>
import Vue from 'vue';
import sortBy from '_/sort-by';
import ChartBar from '@/components/echart/ChartBar';

import { getCollectionOrderList } from '@/api/order-meeting';

export default Vue.extend({
  components: {
    ChartBar,
  },
  props: {
    workspaceId: {
      default() {
        return this.$route.query.workspaceId;
      },
    },
  },
  data() {
    return {
      collectionOrderList: [],
      chartData: {
        headings: [],
        values: [],
      },
      loading: false,
    };
  },
  created() {
    this.getCollectionOrderList();
  },
  methods: {
    async getCollectionOrderList() {
      this.loading = true;

      this.collectionOrderList = sortBy((await getCollectionOrderList(this.workspaceId)).data, 'totalNumber').reverse();

      if (this.collectionOrderList.length === 0) {
        this.$emit('noData');
        return;
      }

      this.collectionOrderList.forEach(item => {
        this.chartData.headings.push(item.collectionName);
        this.chartData.values.push(item.totalNumber);
      });
      this.loading = false;
    },
  },
  computed: {
    totalShoes() {
      return this.collectionOrderList.reduce((total, item) => {
        total += item.totalNumber;
        return total;
      }, 0);
    },
  },
});
</script>

<style lang="scss" scoped>
.chart-box {
  height: 400px;
}
.chart-title {
  margin: 20px 0 5px;
  font-size: 12px;
  color: #03234c;
  text-align: center;
}
</style>
