<template>
  <v-chart
    :options="bar"
    ref="bar"
    theme="ovilia-green"
    autoresize />
</template>

<style>
.echarts {
  width: 100%;
  height: 100%;
}
</style>

<script>
import ECharts from 'vue-echarts';
import 'echarts/lib/chart/bar';

export default {
  components: {
    'v-chart': ECharts,
  },
  props: {
    title: {
      type: String,
      default: '大标题',
    },
    xHeading: {
      type: Array,
      required: true,
    },
    xList: {
      type: Array,
      required: true,
    },
    xAxisName: {
      type: String,
    },
    yAxisName: {
      type: String,
      default: '数量',
    },
    xLableRotate: {
      type: Number,
      default: -20,
    },
    barWidth: {
      type: String,
      default: '35%',
    },
    color: {
      type: String,
      default: '#2F496B',
    },

  },
  data() {
    return {
      bar: {
        title: {
          text: 'TOP10鞋款数据',
          show: true,
          bottom: 0,
        },
        legend: {
          data: ['line'],
        },
        tooltip: {
          trigger: 'axis',
          axisPointer: {
            type: 'cross',
          },
        },
        grid: {
          bottom: 90,
        },
        xAxis: {
          name: this.xAxisName,
          nameTextStyle: {
            fontSize: 14,
          },
          type: 'category',
          data: this.xHeading,
          axisLabel: {
            rotate: this.xLableRotate,
            padding: [10, 0, 0, 0],
          },
          // axisPointer: {
          //   show: true,
          //   type: 'shadow',
          // },
        },
        yAxis: {
          type: 'value',
          name: this.yAxisName,
          nameTextStyle: {
            fontSize: 14,
          },
        },
        series: [{
          data: this.xList,
          type: 'bar',
          barWidth: this.barWidth,
        }],
        color: this.color,
        textStyle: {
          color: '#03234C',
        },
        animationDuration: 2000,
      },
    };
  },
  watch: {
    xHeading(data) {
      this.bar.xAxis.data = data;
    },
    xList(data) {
      this.bar.series[0].data = data;
    },
  },
};
</script>
