<template>
  <div v-loading="loading">
    <template v-if="!loading">
      <div class="chart-box">
        <chart-bar :xHeading="chartData.headings" :xList="chartData.values"
          :xAxisName="$t('order-meeting.statistics.sales_person')" />
      </div>
      <p class="chart-title">
        {{$t('order-meeting.statistics.top10_salesperson_ranking')}}
      </p>
      <div class="table">
        <el-table :data="salesManagerList">
          <el-table-column
            :label="$t('order-meeting.statistics.top10_salesperson_ranking')">
            <template slot-scope="scope">
              {{`TOP${scope.$index + 1}`}}
            </template>
          </el-table-column>
          <el-table-column
            prop="salesManagerName"
            :label="$t('order-meeting.statistics.sales_person')">
          </el-table-column>
          <el-table-column
            prop="shoeNumberSum"
            width="175">
            <template slot-scope="scope">
              {{ scope.row.shoeNumberSum }}{{$t('order-meeting.statistics.units')}}
            </template>
          </el-table-column>
        </el-table>
      </div>
    </template>
  </div>
</template>

<script>
import ChartBar from '@/components/echart/ChartBar';
import sortBy from '_/sort-by';
import { getsalesLeader } from '@/api/order-meeting';

export default {
  components: {
    ChartBar,
  },
  data() {
    return {
      salesManagerList: [],
      chartData: {
        headings: [],
        values: [],
      },
      loading: false,
    };
  },
  created() {
    this.getsalesLeader();
  },
  methods: {
    async getsalesLeader() {
      this.loading = true;

      this.salesManagerList = sortBy((await getsalesLeader(this.$route.query.workspaceId)).data, 'shoeNumberSum').reverse();

      if (this.salesManagerList.length === 0) {
        this.$emit('noData');
        return;
      }
      const list = this.salesManagerList.slice(0, 10);
      list.forEach(item => {
        this.chartData.headings.push(item.salesManagerName);
        this.chartData.values.push(item.shoeNumberSum);
      });
      this.loading = false;
    },
  },
};
</script>

<style lang="scss" scoped>
.chart-box {
  height: 400px;
}
.chart-title {
  margin: 20px 0 5px;
  font-size: 12px;
  color: #03234c;
  text-align: center;
}
</style>
