<template>
  <div :class="{'flex-column': isEmpty}" :style="{'height': isEmpty? '100%' : 'auto'}">
    <div class="nav flex-space-between">
      <nav class="flex" style="height:100%">
        <row class="flex-center nav-item" v-for="(item, index) in analysisTabs" :key="item.name"
          :class="{ 'current': currentTab === index }"
          @click="currentTab=index">
          {{item.name}}
        </row>
      </nav>
    </div>
    <transition name="slide-fade" mode="out-in" appear>
      <component v-if="!isEmpty"
        @noData="isEmpty=true"
        :is="analysisTabs[currentTab].component">
      </component>
      <EmptyData v-else>
      </EmptyData>
    </transition>
  </div>
</template>

<script>
import i18n from 'i18next';

import Shoe from './components/Shoe';
import Series from './components/Series';
// import Comment from './components/Comment';
import Salesperson from './components/Salesperson';
import EmptyData from './EmptyData';


export default {
  name: 'Analysis',
  components: {
    Shoe,
    Series,
    // Comment,
    Salesperson,
    EmptyData,
  },
  data() {
    return {
      analysisTabs: [{
        name: i18n.t('order-meeting.statistics.product_data'),
        component: 'Shoe',
      }, {
      //   name: i18n.t('order-meeting.statistics.comment_data'),
      //   component: 'Comment',
      // }, {
        name: i18n.t('order-meeting.statistics.collection_ranking'),
        component: 'Series',
      }, {
        name: i18n.t('order-meeting.statistics.salesperson_ranking'),
        component: 'Salesperson',
      }],
      isEmpty: false,
      currentTab: 0,
      loadingExport: false,
    };
  },
  created() {
    if (this.$route.query.module) {
      this.currentTab = +this.$route.query.module;
    }
  },
  watch: {
    // 切换标签
    currentTab(val) {
      this.isEmpty = false;
      this.$router.push({
        path: this.$route.path,
        query: {
          ...this.$route.query,
          module: val,
        },
      });
    },
  },
};
</script>

