  position: sticky;
  top: 0;
  background-color: #fff;
  z-index: 9;
  height: 60px;
  padding: 0 24px;
  border-bottom: 1px solid #e6e6e6;
  &-item {
    cursor: pointer;
    height: 100%;
    border-bottom: 4px solid transparent;
    margin-right: 20px;
    color: #868789;
  }
  .current {
    color: #333;
    border-bottom: 4px solid #03234c;
  }
  .export {
    cursor: pointer;
    width: 88px;
    height: 32px;
    background-color: #03234c;
    color: #fff;
    &:hover {
      background-color: #354f70;
    }
    &:active {
      background-color: #021b3b;
    }
  }
  .disable {
    background-color: #ccc;
    cursor: not-allowed;
    &:hover {
      background-color: #ccc;
    }
    &:active {
      background-color: #ccc;
    }
  }
}

.slide-fade-enter-active {
  transition: all 0.3s ease;
}
.slide-fade-leave-active {
  transition: all 0.3s ease;
}
.slide-fade-enter,
.slide-fade-leave-to {
  transform: translateX(20px);
  opacity: 0;
}
</style>
